







import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyPasswrod"
})
export default class MyPasswrod extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    visible: boolean = true;

    get className() {
        return `my-icon-eye-${this.visible ? "close" : "open"}`;
    }
    get initValue() {
        let finalValue = "-";
        if (this.currentValue && this.visible) {
            finalValue = "＊ ＊ ＊ ＊ ＊";
        } else {
            finalValue = this.currentValue;
        }
        return finalValue;
    }
    switchHandle() {
        this.visible = !this.visible;
    }
}
